import React, { useState } from "react";
import {
  Stack,
  Box,
  Heading,
  Card,
  CardBody,
  CardFooter,
  ResponsiveContext,
} from "grommet";
import ProgressiveImage from "react-progressive-image";
import { navigate } from "gatsby";
import Modal from "../../components/Modal";
import FileGallery from "../../components/FileGallery";
import { Braille as Apps, Close } from "grommet-icons";
import { Image } from "../../components";
import { FALLBACK_IMAGE } from "../../constants";
const Component = ({
  dataList,
  onClickAppsIcon,
  showFallBack = false,
  isOwner,
}) => {
  const avatarSrc = "";
  const size = React.useContext(ResponsiveContext);
  const [showModal, setShowModal] = useState(false);
  const [activeCardImage, setActiveCardImage] = useState([]);

  const widthLookup = {
    xxsmall: "100%",
    xsmall: "50%",
    small: "50%",
    medium: "20%",
    xlarge: "20%",
    full: "20%",
  };
  return (
    <Box
      fill={true}
      wrap={true}
      direction="row"
      align="center"
      justify="center"
      alignContent="center"
      margin={{ top: "1%" }}
    >
      {dataList &&
        dataList.map((e, i) => {
          const { thumbnail, title, cta, cardHref } = e;
          const boxProps = cardHref
            ? {
                style: { cursor: "pointer" },
                onClick: () => navigate(cardHref),
              }
            : {};
          return (
            <Box
              {...boxProps}
              outline={false}
              width={widthLookup[size]}
              key={i}
              pad="small"
            >
              <Card elevation="large" width="100%">
                <Stack anchor="bottom-right">
                  {showFallBack && (
                    <CardBody height="small">
                      <Image
                        src={thumbnail && thumbnail[0] && thumbnail[0].url()}
                        onImageClick={() => {
                          setShowModal(true);
                          setActiveCardImage(thumbnail);
                        }}
                        imageProps={{
                          fit: "cover",
                        }}
                      />
                    </CardBody>
                  )}
                </Stack>
                <Stack anchor="top-right">
                  <Box
                    fill={true}
                    justify="center"
                    alignContent="center"
                    align="center"
                  >
                    <Heading level="5" textAlign="center">
                      {title}
                    </Heading>
                  </Box>
                  {onClickAppsIcon && "" && (
                    <Apps
                      size="medium"
                      color="brand"
                      onClick={onClickAppsIcon}
                    />
                  )}
                </Stack>
                <CardFooter align="center">
                  <Box
                    fill={true}
                    justify="center"
                    alignContent="center"
                    align="center"
                  >
                    {cta}
                  </Box>
                </CardFooter>
              </Card>
            </Box>
          );
        })}
      {showModal && activeCardImage && (
        <Modal onClose={() => setShowModal(false)}>
          <Box>
            <FileGallery showUpload={isOwner} files={activeCardImage} />
          </Box>
        </Modal>
      )}
    </Box>
  );
};

export default Component;
